import './user.scss'
import Foot from '~/components/Foot'
import { axios } from '&/axios'
import { Modal, Button, WingBlank, WhiteSpace, Toast } from 'antd-mobile';
import { history } from '&/history'
import Head from '~/components/Head'

const prompt = Modal.prompt;
export default class AdminUser extends Component {

  constructor() {
    super();
    this.state = {
      userlist: []
    }
  }

  componentDidMount() {
    axios.get('/bookstore/getAllUser').then(res => {
      this.setState({
        userlist: res.data.result
      })
    })
  }

  userAdd = () => {
    history.push('/super/adduser')
  }

  deleteUser=(id)=>{
    console.log(id)
    axios.post('/bookstore/deleteUser',{_id:id}).then(res=>{
      this.setState({
        userlist: res.data.result
      })
    })
  }

  render() {
    const { userlist } = this.state;
    return (
      <div className='admin_user'>
        <Head title='图书馆管理账户列表'></Head>
        <div className='user_con'>
          {
            userlist.map((list, i) => {
              return (

                <div style={{backgroundColor:i%2==0?'#ccfdf8':'#e4f8cd'}} key={i} className='user_list'>
                  <p className="user_p1">{list.username}</p>
                  <p className="user_p2">{list.usertel}</p>
                  <p className="user_p3"><span onClick={()=>this.deleteUser(list._id)} className='iconfont icon-shanchu'></span></p>
                </div>

              )
            })
          }
        </div>

        <div className="user_add">
          <span style={{color:'red',fontSize:'20px'}} onClick={this.userAdd} className='iconfont icon-jia'></span>
        </div>
        <div className='user_foot'>
          <Foot></Foot>
        </div>
      </div>
    )
  }
}