import './index.scss'
import {NavLink} from 'react-router-dom'
import {Badge} from "antd-mobile"

export const foots = [
    {txt:"简介管理",path:"/super/store",name:"store",icon:"icon-gonggao1"},
    {txt:"公告管理",path:"/super/notice",name:"notice",icon:"icon-gonggao3"},
    {txt:"用户管理",path:"/super/user",name:"user",icon:"icon-yonhu"},
    {txt:"个人中心",path:"/super/mine",name:"mine",icon:"icon-gerenzhongxin1"}
]


export default class Foot extends Component{
    state={
        foots
    }
    render(){
        return(
            <footer>
                {
                    foots.map((foot,i)=>{
                        return(
                            <div key={i}>
                                <NavLink to={foot.path} activeClassName='nav-active'>
                                    <i className={"iconfont "+foot.icon}></i>
                                    <span>{foot.txt}</span>
                                    {i==2&&<Badge className="hot" hot style={{marginLeft:12}}></Badge>}
                                </NavLink>
                            </div>
                        )
                    })
                }
            </footer>
        )
    }
}